* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

.country-list {
  list-style: none;
  padding-left: 0;
}

.list-item {
  display: flex;
  align-items: baseline;
}

.list-title {
  margin-left: 20px;
  font-weight: 400;
}

.country-info {
  list-style: none;
}

.info-container {
  display: flex;
  align-items: baseline;
}

.info-title {
  margin-left: 20px;
  font-size: 30px;
}

.info-span {
  font-weight: 700;
}
